@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400&display=swap");

.hero_container {
  background-image: url(../assets/hero-bg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  justify-content: space-between;
  height: 85vh;
  width: 100%;
  margin-top: 5em;
}

.accessMainBg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.hero_text_container {
  z-index: 10;
  position: absolute;
  display: flex;
  width: 100%;
  height: 63.8%;
  /* background-color: red; */
  border: 1px solid red;
}

.text_hero {
  display: flex;
  justify-content: space-between;
  color: white;
}

.left_wing {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: start;
  flex: 1;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-around;
}

.right_wing {
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  flex: 1;
  padding-right: 2rem;
}

.left_text_container {
  margin-top: 20%;
  font: normal normal normal 30px/56px Nunito Sans;
  letter-spacing: 0px;
  color: white;
  transition: display 0.2 ease-in 300ms;
}

.right_button_container {
  display: flex;
  flex-direction: column;
}

.carousel_button {
  border: 0;
  background-color: white;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin: 0.3rem auto;
  font-weight: bold;
}

.dots_container {
  display: flex;
  flex-direction: row;
}

.dots {
  height: 10px;
  width: 10px;
  border-radius: 50%;
  background-color: #b3b3b3;
  margin-right: 0.3rem;
}

.dots.active {
  background-color: white;
}

.yellow_text {
  color: #f2b90f;
}

.bold_text {
  font: normal normal 800 30px Nunito Sans;
  color: #edeeef;
}

.about_us {
  min-height: 80vh;
  width: 100%;
  background: #ecf3fd 0% 0% no-repeat padding-box;
  /* padding: 2rem; */
  display: flex;
  justify-content: space-around;
}

.about_img {
  max-width: 100%;
  height: auto;
}

.about_image_container {
  width: 85%;
  height: auto;
  z-index: 10;
}

.tiles {
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-content: center;
  position: relative;
}

.tile1,
.tile2,
.tile3 {
  background-image: url(../assets/brain.png);
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 400px;
  width: 400px;
}

.tile2 {
  background-image: url(../assets/people.png) !important;
}

.tile3 {
  background-image: url(../assets/stats.png) !important;
}

.tile_image {
  max-width: 100%;
  height: 100%;
}

.tile {
  height: 28%;
  width: 300px;
  position: relative;
  display: block;
}

.tile_text {
  position: absolute;
  top: 53%;
  left: 10%;
  right: 10%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #484848;
  opacity: 0.9;
  font-size: 14px;
  font-weight: 400;
  text-align: center;
}

.half_block {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */
  flex: 1;
  margin: 3rem auto;
  padding: 0 5rem;
}

.ace_heading {
  text-align: left;
  font: normal normal 700 24px/30px Nunito Sans;
  letter-spacing: 0px;
  color: #15499e;
  opacity: 1;
  margin-bottom: 1rem;
}

.ace_left_heading {
  text-align: left;
  font: normal normal 700 24px/30px Nunito Sans;
  letter-spacing: 0.8px;
  color: #171717;
  opacity: 1;
  margin-bottom: 1rem;
}

.underline {
  height: 2px;
  width: 50px;
  background-color: #ed367f;
  border: 0;
  margin-bottom: 1rem;
}

.right_block_text {
  text-align: left;
  margin-bottom: 2rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #484848;
  opacity: 0.9;
  font-size: 15px;
  font-weight: 600;
}

.eu {
  width: 100%;
  height: auto;
}

.text_center {
  display: flex;
  justify-content: center;
}

.last_section {
  margin-top: 5rem;
  margin-bottom: 2%;
}

.last_section_card_wrapper {
  width: 100%;
  height: auto;
  padding-top: 1%;
  padding-bottom: 1%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ace_last_heading {
  text-align: left;
  font: normal normal bold 35px/50px Nunito Sans;
  letter-spacing: 0.8px;
  color: #171717;
  opacity: 1;
  margin-bottom: 1rem;
  text-align: center;
}

.left_team_deco {
  position: absolute;
  top: 25%;
  left: -30%;
}

.right_team_deco {
  position: absolute;
  top: 25%;
  right: -30%;
  height: 10px;
}

.left_about_deco {
  position: absolute;
  left: 0%;
}

.right_about_deco {
  position: absolute;
  right: 0%;
}