.card_container {
  background-color: #0c4da1;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 250px;
  height: 270px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 1%;
}
.card_img {
  width: 130px;
  height: 130px;
  object-fit: cover;
  object-position: center;
}
.bold_text,
.light_text {
  width: 100%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #eff4fc;
  font-size: 18px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 1%;
}
.light_text {
  font-size: 12px;
  font-weight: 400;
  color: #afc6ea;
}
