.market_intelligence_body {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5em;
  padding: 1em 0;
}


.submain {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.main_text_bold {
  width: 100%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #15499e;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 2%;
}

.search_div {
  width: 80%;
  height: 38px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 0.30000001192092896px solid #b4b2b2;
}

.search_div input[type="text"] {
  width: 71%;
  height: 100%;
  border: none;
  outline: none;
  color: #171717;
  opacity: 0.8;
}

.card_wrapper {
  width: 100%;
  height: fit-content;
  padding-top: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: row !important;
  flex-wrap: wrap;
}

.card {
  width: 43%;
  height: 275px;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  box-shadow: 0px 1px 2px 1px lightgray;
  padding-left: 1%;
  margin: 2%;
  text-decoration: none;
}

.card_dotted {
  width: 43%;
  height: 275px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: 2px dashed rgb(168, 220, 237);
  align-items: center;
  cursor: pointer;
  padding-left: 1%;
  margin: 2%;
  text-decoration: none;
}

.main_dotted_card_image {
  width: 80px;
  height: 80px;
  object-fit: cover;
  object-position: center;
}

.main_card_image {
  width: 100%;
  object-fit: cover;
  object-position: center;
  margin-top: 4%;
}

.card_first_text {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #484848;
  opacity: 0.9;
  font-size: 15px;
  font-weight: 600;
  margin-left: 1%;
  margin-top: 3%;
  padding-bottom: 2%;
}

.sub_card_wrapper {
  width: 100%;
  height: 25%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: start;
  padding-left: 1%;
  margin-bottom: 1.5%;
}

.dotted_blue_text {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: rgb(34 116 224);
  opacity: 0.8;
  font-size: 14px;
  font-weight: bold;
}

.subtext {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #484848;
  opacity: 0.9;
  font-size: 13px;
  font-weight: 600;
  margin-left: 1%;
  padding-bottom: 2%;
  text-align: center;
}