.main {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow-x: hidden;
    overflow-y: auto;
}


.header {
    width: 100vw;
    height: 5em;
    position: fixed;
    top: 0;
    background-color: rgb(236, 231, 231);
    z-index: 999;
    box-shadow: 1px 1px 1px 1px whitesmoke;
}

body {
    overflow-x: hidden;
}


.footer {
    width: 100%;
    height: 10%;
}