.main {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #131313e1;
  position: fixed;
  overflow-y: hidden;
}

.submain {
  width: 425px;
  height: 325px;
  /* UI Properties */
  background: #ffffff 0% 0% no-repeat padding-box;
  opacity: 1;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}
.closeBtn {
  width: 100%;
  height: 12%;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: end;
}
.close_btn_image {
  width: 28px;
  height: 28px;
  padding-right: 2%;
  cursor: pointer;
}
.modal_bottom {
  width: 100%;
  height: 88%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.modalEnvelope {
  width: 80px;
  height: 80px;
}
.get_in_touch {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bolder;
  font-size: 25px;
  letter-spacing: 0.64px;
  color: #15499e;
  margin-top: 5%;
}
.incase {
  text-align: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #171717;
  opacity: 0.8;
  font-size: 14px;
  font-weight: 600;
  margin-top: 1.5%;
  margin-bottom: 5%;
}
.link_text {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bolder;
  font-size: 20px;
  letter-spacing: 0px;
  color: #15499e;
}
