@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap");

.header_container {
  display: flex;
  background-color: #ffffff;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 100%;
  font-family: "Poppins", sans-serif;
}
.header_logo {
  height: 100px;
  width: 150px;
  object-fit: contain;
  object-position: center;
  margin-left: 10%;
}
.image_container {
  display: flex;
  padding: 0 1rem;
}
.menu_container {
  display: flex;
  flex: 1;
  justify-content: flex-end;
  align-items: center;
  padding: 0 3rem;
}
.menu_option {
  margin: 0px 10px;
}
.contact_button {
  background: #ed367f 0% 0% no-repeat padding-box;
  padding: 15px 45px;
  border: none;
  color: white;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .footer_container {
    padding: 25px 0;
  }
}
