.img_main {
  width: 100%;
  height: 90%;
}

.accessMainBg {
  width: 100%;
  /* height: 600px; */
  object-fit: contain;
  object-position: center;
}

.bold_text {
  width: auto;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: white;
  bottom: 62%;
  right: 63%;
  /* margin-left: 80px; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: -5px;
}

.sub_main_text {
  margin-left: 190x;
  width: 410px;
  height: auto;
  color: white;
  position: absolute;
  bottom: 49%;
  right: 60.5%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  opacity: 0.8;
  font-size: 13px;

}

@media only screen and (max-width: 525px) {
  .sub_main_text {
    width: 100%;
    position: absolute;
    bottom: 49%;
    right: 60.5%;

  }
}

.mainNormalText {
  font-weight: 350;
  font-size: 32px;
  opacity: 0.8;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.mainBoldText {
  font-weight: bolder;
  font-size: 32px;
  font-family: Arial, Helvetica, sans-serif;
}

.genie_section {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 5em 0;
}

.sub_genie_section {
  width: 70%;
  height: 60%;
  display: flex;
  justify-content: flex-start;
  align-items: start;
  flex-direction: column;
}

.normal_txt {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #171717;
  opacity: 0.8;
  font-size: 14px;
  font-weight: 600;
}

.normal_txt_bold {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: rgb(34 116 224);
  opacity: 0.8;
  font-size: 14px;
  font-weight: bold;
}

.card_wrapper {
  width: 70%;
  height: 95%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}