.card {
  width: 45%;
  height: 100%;
  box-shadow: 0px 1px 2px 1px lightgray;
  display: flex;
  justify-content: flex-start;
  align-items: start;
  flex-direction: column;
  border-radius: 2px;
  margin-right: 5%;
}
.card img {
  width: 95%;
  height: 85%;
  object-fit: cover;
  object-position: center;
  margin-left: 3%;
  margin-top: 2%;
}
.normal_txt_1 {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #171717;
  opacity: 0.9;
  font-size: 14px;
  font-weight: bold;
  margin-left: 3%;
  margin-top: 3%;
  padding-bottom: 2%;
}
