.testimonial_body {
  width: 100%;
  height: calc(100vh - 12em);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5em;
}

.submain {
  width: 50%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 1em 0;
}

.main_text_bold {
  width: 100%;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 2px;
  color: #000;
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 1%;
}

.card_wrapper {
  width: 100%;
  height: fit-content;
  padding-top: 1%;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: row !important;
  flex-wrap: wrap;
}

.horizontal_line {
  width: 100%;
  height: auto;
  margin-bottom: 1.5%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.line {
  width: 40px;
  border-bottom: 2px solid #ed367f;
}

.card {
  width: 50%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  padding-left: 1%;
  margin-top: 2%;
}

.main_dotted_card_image {
  width: 80px;
  height: 80px;
  object-fit: cover;
  object-position: center;
}

.main_card_image {
  width: 100%;
  object-fit: cover;
  object-position: center;
  margin-top: 4%;
}

.card_first_text {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #484848;
  opacity: 0.9;
  font-size: 15px;
  font-weight: 600;
  margin-left: 1%;
  margin-top: 3%;
  padding-bottom: 2%;
}

.player {
  position: relative;
  bottom: 83%;
  width: 30px;
  height: 30px;
  float: right;
  left: 85%;
  cursor: pointer;
}