.card {
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: center;
  box-shadow: 0px 1px 2px 1px lightgray;
  padding: 10px;
  margin: 2%;
  flex: 1 1 40%;
}
.main_card_image {
  width: 100%;
  object-fit: contain;
  object-position: center;
}
.card_first_text {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #484848;
  opacity: 0.9;
  font-size: 15px;
  font-weight: 600;
  margin-left: 1%;
  margin-top: 3%;
  padding-bottom: 2%;
}
.sub_card_wrapper {
  width: 100%;
  height: 25%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: start;
  padding-left: 1%;
  margin-bottom: 1.5%;
}
.flag_wrapper,
.flag_wrapper1 {
  width: 20%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: start;
  justify-content: space-around;
  margin-right: 2%;
  text-decoration: none;
}
.flag_wrapper img {
  width: 50px;
  height: 30px;
  object-fit: cover;
  object-position: center;
  border: 3px;
}
.flag_wrapper1 img {
  width: 40px;
  height: 40px;
  object-fit: contain;
  object-position: center;
  border: 3px;
}
.flag_wrapper span,
.flag_wrapper1 span {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  letter-spacing: 0px;
  color: #484848;
  opacity: 0.9;
  font-size: 11px;
  font-weight: 600;
  margin-left: 1%;
  margin-top: 3%;
  padding-bottom: 2%;
}
.last_text {
  color: #979797 !important;
  opacity: 0.5;
}
